  pointer-events: none;
  visibility: hidden;
}

.login-modal {
  width: 320px;
  background-color: #fff;
  padding: 40px 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.login-modal input {
  width: 100%;
  border: 1px solid #545454;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 5px 10px;
}

